.container{
    width: 400px;
    height: 729px;
    margin: auto;
    margin-top: 75px;
    margin-bottom: 75px;
    border-radius: 12px;
    background-image: linear-gradient(180deg, #130754 0%, #3b2f80 100%);
}
.top-bar{
    display: flex;
    justify-content: center;
    gap: 14px;
    padding-top: 60px;
}
.top-bar input{
    display: flex;
    width: 150px;
    height: 30px;
    background: #ebfffc;
    border: none;
    outline: none;
    border-radius: 30px;
    padding-left: 30px;
    color: #626262;
    font-size: 15px;
    font-weight: 800;
}

.search-icon{
    display: flex;
    justify-content: center;
    align-items: center;
    width: 58px;
    height: 58px;
    background: #ebfffc;
    border-radius: 40px;
    cursor: pointer;
}

.weather-image{
    display: flex;
    justify-content: center;
}

.weather-temp{
    display: flex;
    justify-content: center;
    color: white;
    font-size: 60px;
    font-weight: 400;
}

.weather-location{
    display: flex;
    justify-content: center;
    color: white;
    font-size: 38px;
    margin-bottom: 20px;
}

.humidity-percent{
    display: flex;
    justify-content: center;
    color: white;
    font-size: 30px;
    font-weight: 400;
}

.text{
    color: #fff;
}